.navbar{
    margin-top: 20px;
}

.navbar a{
    margin: 0px 20px;
    color: tomato;
    font-size: 20px;
    text-transform: capitalize;
    font-weight: 600;
    text-decoration: none;
    border-bottom: 2px transparent;
    transition: .3s;
    cursor: pointer;
}
.navbar a:hover{
    border-bottom: 2px solid tomato;
}